
import slide from 'components/slide/index'
import http from 'js/http'
import InlineLoading from 'vux/src/components/inline-loading/index.vue'


export default {
name: "index",
components: {
slide,
InlineLoading
},
data() {
return {
tabList: [
{
type: '1',
name: '收入明细',
},
{
type: '0',
name: '支出记录'
}
],
currentType: localStorage.getItem("currencyDetailType") || '1',
dataList: [],
page: 1,
loadShow: true,
currencyInfo: {},
dtsId: this.$route.params.id,
total:'',
refreshLoad:false
}
},
methods: {
refreshData(){
this.refreshLoad = true
http.post('/wallet/recharge-refresh', {
id: this.$route.params.id,
}, (res) => {

this.refreshLoad = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
if (res.content.isRefresh){
this.getCurrencyInfo()
this.page = 1
this.dataList = []
this.total = ''
this.$refs.my_scroller.finishInfinite(false);
}
})
},
goFrozen() {
this.$router.push({
path: `/assets/dts${this.dtsId}/frozen`
})
},

selectedTab(item) {
if (item.type === this.currentType) return
this.currentType = item.type
localStorage.setItem("currencyDetailType", item.type);
this.page = 1
this.dataList = []
this.total = ''
this.$refs.my_scroller.finishInfinite(false);
},
handleBottom() {
if (this.total!==''&&this.dataList.length >= parseInt(this.total)){
this.$refs.my_scroller.finishInfinite(true);
return
}
http.post('/wallet/currency-detail', {
id: this.$route.params.id,
type: this.currentType,
page: this.page,
page_size: 10
}, (res) => {
if (this.loadShow) this.loadShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.dataList = this.dataList.concat(res.content.list)
this.total = res.content.count
this.page++
this.$refs.my_scroller.finishInfinite(false);
})
},
getList() {
http.post('/wallet/currency-detail', {
id: this.$route.params.id,
type: this.currentType,
page: this.page,
page_size: 10
}, (res) => {
if (this.loadShow) this.loadShow = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.dataList = this.dataList.concat(res.content.list)
if (this.dataList.length < parseInt(res.content.count)) {
this.$refs.vueLoad.onBottomLoaded();
} else {
this.$refs.vueLoad.onBottomLoaded(false);
}
})
},
getCurrencyInfo() {
http.post('/wallet/currency-info', {
id: this.$route.params.id,
}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
this.currencyInfo = res.content

})
}
},
created() {

this.getCurrencyInfo()

},
destroyed() {
localStorage.removeItem('currentType')
}
}



